.page-title {
  padding: 0.5em 1.5em;
}
.flex-1 {
  flex: 1 1;
}
.floating-edit-button-wrapper {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background: #00b0f0;
  box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.12);
  position: absolute;
  top: 10%;
  right: 5%;
  border: none;
  display: flex;
}
.floating-edit-button-wrapper.\--profile-picture {
  top: 80%;
  left: 70%;
}
.floating-edit-button-wrapper svg {
  margin: auto;
}
.floating-edit-button-wrapper:hover {
  box-shadow: 3px 6px 6px rgba(0, 0, 0, 0.12);
  cursor: pointer;
}
.friends-list-container {
  border-radius: 8px;
  background: #ffffff;
  flex: 1 1;
  align-self: flex-start;
}
.friends-list-header {
  padding: 1em 1em;
  border-top-right-radius: 8px;
  border-top-left-radius: 8px;
}
.friends-list-header h5,
.friends-list-header p {
  margin-bottom: 0.2em;
}
.friends-search-container {
  padding: 0.5em;
  background: #f1f1f1;
}

.friends-search-container .searchContainer {
  display: inline-flex;
  border: 1px solid #c4c4c4;
  border-radius: 4px;
  overflow: hidden;
  max-height: 40px;
  width: 90%;
  align-items: center;
}
.friends-search-container.chatbox .searchContainer {
  margin: 0.6em 1em;
}
.searchIconContainer {
  background: #f1f1f1;
  margin: 0;
  height: 100%;
}
.searchIcon {
  padding: 0.8rem;
  font-size: 16px;
}

.searchBox {
  border: 0;
  padding: 0.5rem 0.5rem 0.5rem 0.5rem;
  flex: 1 1;
}
.friends-list {
  padding: 1em;
}
.notifications-container {
  background: #ffffff;
  border: 1px solid #f1f1f1;
  border-radius: 4px;
  width: 100%;
}
.notifications-page-header {
  padding: 0.5em 1em;
  border-bottom: 1px solid #f1f1f1;
}
.single-notification {
  display: flex;
  padding: 1em 2em;
  justify-content: space-between;
  border-bottom: 1px solid #f1f1f1;
}
.notification-owner-wrapper {
  width: 30px;
  height: 30px;
  overflow: hidden;
  border-radius: 15px;
}
.notification-owner {
  width: 100%;
  height: 100%;
}
.single-notification-link, 
.single-notification-link:hover, 
.single-notification-link:visited, 
.single-notification-link:active {
  color: inherit !important;
  display: flex;
  text-decoration: none;
}
.chat-list {
  background: #fff;
  overflow: auto;
  height: calc(100vh - 255px);
}
.chat-head {
  padding: 16px;
  border: 1px solid #F0F0F0;
  display: flex;
}
.chat-head-photo {
  height: 40px;
  width: 40px;
  overflow: hidden;
  border-radius: 50%;
  margin-right: 10px;
}
.chat-head-photo > img {
  width: 100%;
  height: 100%;
}
.chat-head-content {
  width: calc(100% - 50px);
}
.chat-head-user-and-time {
  display: flex;
  justify-content: space-between;
}
.chat-head p {
  margin-bottom: 2px;
}
.previous-chat {
  overflow: hidden;
  width: 80%;
  text-overflow: ellipsis;  
  white-space: nowrap;
}
.chat-head-active, .chat-head:hover {
  background: #E8F6FB;
  cursor: pointer;
}
.chat-head a {
  text-decoration: none;
}
.message-count {
  color: #fff;
  background: #09526D;
  border-radius: 2px;
  font-size: 12px !important;
  padding-left: 4px;
  padding-right: 4px;
  height: 18px;
  min-width: 18px;
  text-align: center;
}
.chat-header {
  display: flex;
  padding: 16px;
  border: 1px solid #F0F0F0;
}
.main-chat-container {
  background: #fff;
  height: calc(100vh - 120px);
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.close-chat {
  background: transparent;
  border: none;
  font-weight: 800;
  cursor: pointer;
}
.chat-interface {
  padding: 16px;
  flex: 1 1;
  overflow: auto;
  flex-direction: column;
  padding-bottom: 0;
}
.chat-message-wrapper {
  display: flex;
  padding-bottom: 16px;
}
.chat-message-wrapper.owner {
  justify-content: flex-end;
} 
.message-owner {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  overflow: hidden;
  align-self: flex-end;
  margin-right: 12px;
  margin-bottom: 8px;
}
.message-owner img {
  width: 100%;
  height: 100%;
}
.message-wrapper {
  max-width: 300px;
}
.message-wrapper p {
  margin-bottom: 0;
}
.other-chat-message {
  background:  #E1E1E1;
  border-radius: 6px;
  padding: 0.5em 1em;
}
.owner-chat-message {
  background:  #DBEDF4;;
  border-radius: 6px;
  padding: 0.5em 1em;
}
.owner .message-owner {
  margin-left: 12px;
  margin-right: 0;
}
.message-time {
  text-align: right;
}
.chat-input-wrapper {
  padding: 1em;
  background: #fafafa;
  margin: 0 -16px;
  display: flex;
  position: -webkit-sticky;
  position: sticky;
  bottom: 0;
}
.chat-input-wrapper .form-control {
  flex: 1 1;
}
.chat-input-wrapper .btn {
  margin: 0;
  padding: 0.5em 1em;
  margin-left: 1em;
}
.countdown {
  border-radius: 8px;
}
.countdown>h3, .countdown>p {
  font-weight: 900;
}
.suggestions {
  border-radius: 4px;
  background: #ffffff;
  margin-bottom: 30px;
}
.suggestions-header {
  padding: 1em;
  border-top-right-radius: 4px;
  border-top-left-radius: 4px;
}
.text-gclout-blue:hover {
  text-decoration: none;
  color: #00b0f0;
}
.suggestion-image-wrapper {
  width: 40px;
  height: 40px;
  border-radius: 100%;
  margin-right: 4px;
  overflow: hidden;
}
.suggestion-image {
  width: 100%;
  height: 100%;
}
.single-suggestion {
  display: flex;
  align-content: center;
  padding: 0.5em;
}
.single-suggestion a,
.single-suggestion p {
  font-size: 14px;
  margin-bottom: 0;
}
.dashed-border-bottom {
  border-bottom: 1px dashed #c4c4c4;
}
.suggestion-footer {
  padding: 1em 1em;
  padding-top: 0.1em;
}
.suggestion-footer-link {
  color: #00b0f0;
  font-size: 16px;
}
.suggestion-footer-link:hover {
  cursor: pointer;
  text-decoration: none;
  color: #333333;
}
.suggested-friend-actions {
  display: flex;
  align-self: center;
}
.trending-section-header {
  padding: 1em;
}
.trending-section {
  background: #ffffff;
  border-radius: 4px;
}
.trending-image-wrapper {
  width: 40px;
  height: 40px;
  border-radius: 100%;
  margin-right: 1em;
  overflow: hidden;
}
.trending-image {
  width: 100%;
  height: 100%;
}
.trending-name p {
  margin-bottom: 0;
  font-size: 16px;
}
.trending-name {
  margin: auto 0;
}

.trending-section-content {
  padding: 1em;
  border-radius: 4px;
  padding-top: 0;
}

.trending-text p {
  font-size: 14px;
  text-align: justify;
  margin-top: 0.5em;
}
.like-btn {
  background: transparent;
  border: none;
  padding: 0;
}
.like-btn:hover {
  cursor: pointer;
  color: #00b0f0;
}
.trending-post {
  padding: 1em 0;
}

.post-link,
.post-link:hover,
.post-link:focus,
.post-link:visited,
.post-link:active {
  color: #333333;
  text-decoration: none;
}
.post-container {
  background: #ffffff;
  padding: 1em 2em;
}
.post-container:hover {
  background: #fdfdfd;
}
.post-owner {
  display: flex;
  align-content: center;
}
.post-owner-image-wrapper {
  width: 50px;
  height: 50px;
  border-radius: 100px;
  overflow: hidden;
  margin-right: 1em;
}
.post-owner-image {
  width: 100%;
  height: 100%;
}
.post-owner-details p {
  font-weight: 600;
  margin: auto 0;
}
.post-owner-details {
  align-self: center;
}
.post-type {
  opacity: 0.7;
  font-weight: 400 !important;
}
.post-content > p {
  text-align: justify;
}
.post-actions-container {
  padding: 1em 2em;
  background: #f1f1f1;
  display: flex;
  justify-content: space-between;
}
.post-action {
  background: transparent;
  border: none;
  line-height: 24px;
  font-size: 16px;
}
.post-action .MuiSvgIcon-root-1 {
    font-size: 19px !important;
}
.flipped-reply {
  -webkit-transform: matrix(-1, 0, 0, 1, 0, 0);
          transform: matrix(-1, 0, 0, 1, 0, 0);
}
.post-action:hover, .post-action.active {
  cursor: pointer;
  color: #00b0f0;
}
.post-media-container {
  width: 100%;
}
.post-image {
  width: 100% !important;
}
.petition-header {
  background: #ffffff;
  border-bottom: 1px solid #cfcfcf;
  display: flex;
  justify-content: space-between;
  padding: 0.5em 1em;
}
.petition-header p , .petition-header a{
  margin-bottom: 0;
  padding-bottom: 0;
  text-decoration: none;
  color: #333333;
}
.styled-text {
  margin-bottom: 0;
  padding: 0;
  align-self: center;
}
.post-actions-container .btn {
  margin: 0;
}
.dropup {
  padding: 1em 3em;
  border-radius: 8px;
  display: none;
  left: -4em;
  margin-top: -200px
}
.dropup.show {
  display: block;
}
.dropup-list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  list-style: none;
  justify-content: space-around;
  align-content: center;
}
.dropup-list-item button {
  border: none;
  margin: 4px;
  background: #00b0f0;
  border-radius: 50px;
  padding: 0.4em;
  box-shadow: 2px 2px 8px rgba(0,0 ,0,0.2);
}
.dropup-list-item button:hover {
  box-shadow: 4px 4px 8px rgba(0,0 ,0,0.2);
  -webkit-transform: translateY(-1px);
          transform: translateY(-1px);
}
.dropup-list-item a{
  padding: 0.9em;
  font-size: 10px;
  color: #ffffff;
  border-radius: 50%;
}
.comment-input-wrapper {
  background: #ffffff;
  display: flex;
  align-content: center;
  padding: 1em 2em;
}
.comment-wrapper.hidden {
  display: none !important;
}
.comment-wrapper {
  display: block;
  background: #ffffff;
}
.comment-owner-wrapper {
  width: 40px !important;
  height: 40px !important;
  overflow: hidden;
  border-radius: 30px;
}
.comment-owner {
  width: 100%;
  height: 100%;
}
.comment-input-wrapper .form-control {
  margin-left: 1em;
  flex: 1 1;
}
.previous-comments {
  background:  #F7F7F7;
  padding: 1em 2em;
}
.single-comment {
  display: flex;
  border-bottom: 1px solid #cecece;
  margin-bottom: 1em;
}
.single-comment .comment-owner-wrapper {
  width: 30px !important;
  height: 30px !important;
  overflow: hidden;
  border-radius: 50%;
  margin-right: 1em
}

